.container {
	--border-radius: 10px;

	--color-light:   #29e52d;
	--color-dark:    #d82228;
	--color-neitral: #ffff00;

	--color-shadow-light:   rgba(41, 229, 45, .4);
	--color-shadow-dark:    rgba(216, 34, 40, .4);
	--color-shadow-neitral: rgba(224, 224, 81, 0.4);
}
.container {
	display: flex;
	flex-wrap: wrap;
	margin-top: 30px;
	justify-content: center;
}
.item {
	position: relative;
	top: 0;
	left: 0;
	width: 250px;
	height: 450px;
	margin-right: var(--spacing-medium);
	border-radius: var(--border-radius);
	box-shadow: var(--box-shadow-black);
	transition: var(--transition);
	cursor: pointer;
	margin-right: 20px;
}
.item__header {
	position: absolute;
	bottom: 10px;
	left: 0;
	width: 100%;
	margin-bottom: var(--spacing-small);
	font-size: var(--font-size-subheader);
	text-align: center;
}
.item__img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center center;
	border-radius: var(--border-radius);
}

/***************************************************
COLOR THEME
/***************************************************/
.item__light {
	text-shadow: 0 0 5px currentColor;
	color: var(--color-light);
}
.item__dark {
	text-shadow: 0 0 5px currentColor;
	color: var(--color-dark);
}
.item__neitral {
	text-shadow: 0 0 5px currentColor;
	color: var(--color-neitral);
}

.item__light:hover {
	box-shadow: 0 0 7px 2px var(--color-shadow-light);
}
.item__dark:hover {
	box-shadow: 0 0 7px 2px var(--color-shadow-dark);
}
.item__neitral:hover {
	box-shadow: 0 0 7px 2px var(--color-shadow-neitral);
}

@media screen and (max-width: 900px) {
	.item {
			width: 200px;
			height: 350px;
	}
}
@media screen and (max-width: 750px) {
	.item {
			height: 300px;
			margin: var(--spacing-small);
	}
}