.list__container {
	column-count: 3;
	margin: 20px 0;
	padding: 0;
	list-style-type: none;
}
.list__item {

}
.list__item a {
	display: inline-flex;
	color: black;
	text-decoration: none;
	text-align: center;
	cursor: pointer;
}
.person__photo {
	width: 70px;
	height: 70px;
	object-fit: cover;
	object-position: center center;
	border-radius: 3px;
}
.person__name {
	display: flex;
	align-items: center;
	margin: 0;
	padding: 0 5px;
	color: white;
	text-shadow: 0 0 2px white;
}
.person__comment {
	color: white;
}

@media screen and (max-width: 850px) {
	.list__container {
			column-count: 2;
	}  
}